import React from "react";
import { Link } from 'react-router-dom'


export function Header() {
    return(
        <div className="container mt-2">
            <nav className="navbar navbar-inverse navbar-expand-lg navbar-light bg-light p-3 shadow">
                <a className="navbar-brand" href="/">UCRÓN {process.env.REACT_APP_PC}</a>
                <button className="navbar-toggler" type="button">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse">
                    <div className="navbar-nav">
                        <a href="http://ucron.ml/" className="nav-item nav-link">Home</a>
                        <a href="http://ucron.ml/admins" className="nav-item nav-link">Administradores</a>
                        <a href="http://ucron.ml/developers" className="nav-item nav-link">Desarrolladores</a>
                        <Link to="/deptos" className="nav-item nav-link">Función Pública</Link>
                        <a href="http://ucroneconomia.ml/" className="nav-item nav-link">Económico</a>
                    </div>
                </div>
            </nav>
        </div>
    )
}