import React from "react";
import { BrowserRouter, Routes, Route } from 'react-router-dom';

import { Economia } from './pages/economia'


function App() {
  return (
    <div className="App">
      <BrowserRouter>
          <Routes>
              <Route path='/' element={ <Economia/> } />
          </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
